body {
  margin: 0;
  padding: 0;
  font-family: ff-meta-serif-web-pro, "Droid Serif", Georgia, serif;
  font-size: 1.1em;
}

h1, h2, h3, h4, h5, h6 {
  font-family: sans-serif;
}

code {

}

pre {
  display: block;
  padding: 6px 12px;
  border: 1px solid cornflowerblue;
  background-color: lavender;
  border-radius: 2px;
  margin-bottom: 12px;
  overflow: scroll;
}

code, pre {
  font-family: Menlo, monospace;
}

.demo-app {
  margin: 0 auto;
  padding-top: 48px;
  padding-bottom: 48px;
  max-width: 800px;
}

.demo-app__content {
  margin-bottom: 24px;
}

.demo-app__logo {
  height: 64px;
  display: block;
  margin-bottom: 12px;
}



